.stopwatch {
  box-sizing: border-box;
  background-color: #ffebcd;
  border: 40px double #008b8b;
  border-radius: 80vmin;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hour-hand {
  transform-origin: bottom;
  background-color: #4682b4;
  border-radius: 60px;
  width: 20px;
  height: 35%;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #add8e6;
  border-radius: 60px;
  width: 10px;
  height: 50%;
  animation: 5s linear infinite move;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center {
  background-color: #d3d3d3;
  border-radius: 60px;
  width: 60px;
  height: 60px;
  position: absolute;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

